import { Component } from '@angular/core';

export class navItem {
  name: string;
  link: string;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})



export class AppComponent {
  title: string = 'Holy';
  navItems: navItem[] = [
    { name: "Home", link: "Home" },
    { name: "Catalog", link: "Catalog" },
    { name: "Contact-Us", link: "Contact-Us" }
  ];
}



